




















	export default{
		components:{},
		data() {
			return{
				oldPwd: '', //旧密码
				newPwd: '',	//新密码	
				newsPwd: '', //再输入的新密码
				Signboard:'',
				Signguid:''
			}
		},
		onLoad:function () {
// 			var jsonList=uni.getStorageSync("jsonList");
// 			var data = eval('(' + jsonList + ')'); //JSON字符串转对象
// 			this.Signboard = data.Ident_Signboard;
// 			this.Signguid = data.Ident_Signguid;
			var jsonList=uni.getStorageSync("jsonList");
			var data=JSON.parse(jsonList);
			this.Signboard=data.Ident_Signboard;
			this.Signguid=data.Ident_Signguid;
		},
		methods:{
			oldPassword : function(e){	//获取输入的旧密码
				this.oldPwd = e.target.value;
			},
			newPassword : function(e){	//获取输入的新密码
				this.newPwd = e.target.value;
			},
			newsPassword : function(e){	  //获取第二次输入的新密码
				this.newsPwd = e.target.value;
			},
			ChangePwd(e){
				if(this.oldPwd == '' || this.newPwd == '' || this.newsPwd == ''){
					uni.showToast({
						title: '请将信息填写完整，不允许为空',
						icon:"none"
					})
				}else if ((!RegExp(/^[A-Za-z0-9]{5,16}$/).test(this.oldPwd)) || (!RegExp(/^[A-Za-z0-9]{5,16}$/).test(this.newPwd))||(!RegExp(/^[A-Za-z0-9]{5,16}$/).test(this.newsPwd))) {
					uni.showToast({
						title: '密码格式不正确',
						icon:"none"
					})
				}else if (this.newPwd!=this.newsPwd) {
					uni.showToast({
						title: '两次密码输入不一致',
						icon:"none"
					})
				}else{
					uni.request({
						url:'http://192.168.199.194/mobile/Security/updatePassword',
						method:'GET',
						data:{
							oldpwd:this.oldPwd,
							newpwd:this.newPwd,
							Ident_Signboard: this.Signboard,
							Ident_Signguid: this.Signguid
						},
						success: (res) => {
							if(res.data.status==0){
								uni.showToast({
									title: res.data.message,
									icon:'none',
								})
							}else{
								uni.showToast({
									title:'登录密码修改失败',
									icon:'none',
								})
							}
						}
					})
				}
				
			}
		}
	}
	
